:root {
    --primary-color: #1976d2; /* Modern blue */
    --primary-light: #e3f2fd; /* Light blue for range background */
    --text-color: #333;
    --light-text-color: #666;
    --border-color: #ddd;
    --background-color: transparent;
    --white: #fff;
    --shadow-light: rgba(0, 0, 0, 0.08);
    --shadow-medium: rgba(0, 0, 0, 0.15);
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: var(--background-color);
    margin: 0;
    color: var(--text-color);
    line-height: 1.6;
}

.container {
    background-color: var(--white);
    padding: 35px 40px;
    border-radius: 12px;
    box-shadow: 0 10px 30px var(--shadow-light);
    text-align: center;
    max-width: 450px;
    width: 90%;
}

h1 {
    font-size: 1.8em;
    color: var(--primary-color);
    margin-bottom: 15px;
    font-weight: 600;
}

p {
    font-size: 0.95em;
    color: var(--light-text-color);
    margin-top: 15px;
}

input[type="text"] {
    padding: 12px 20px;
    font-size: 1.1em;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    width: calc(100% - 40px); /* Adjust for padding */
    max-width: 300px;
    text-align: center;
    cursor: pointer;
    outline: none;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
    margin-bottom: 20px;
}

input[type="text"]:focus {
    border-color: var(--primary-color);
    box-shadow: 0 0 0 3px rgba(25, 118, 210, 0.2); /* Light primary glow */
}

/* Month/Year container and children styling */
.month-year {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
  height: 100%;
}

.month-span {
  display: inline-block;
  vertical-align: middle;
}

.year-text {


  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
}



.year-input {
    width: 50px !important; /* Give it a bit more room for a 4-digit year */
    text-align: center;

    /* Remove these properties: */
    height: 0.2em; 

    margin-bottom: 0px !important;
    vertical-align: middle ; /* Align vertically with other inline elements */
    padding: 2px 0; /* Add some vertical padding for visual height control */

}
/* Calendar Styles */
.calendar { 
    position: absolute;
    background-color: var(--white);
    border: 1px solid var(--border-color);
    border-radius: 8px;
    top:10px;
    z-index: 0;
    padding: 0;
    box-sizing: border-box;
    transform-origin: top center;
    opacity: 1;
    transform: translateY(0);
    transition: opacity 0.3s ease, transform 0.3s ease;
    display: flex;
    flex-direction: column; /* Make calendar a column flexbox */
    min-width: 700px; /* Adjust as needed for your layout */
    min-height: 380px; /* Ensures enough space for content and footer */
    height: 96vh;
}

.calendar.hidden {
    opacity: 0;
    transform: translateY(-10px);
    pointer-events: none;
}

/* Container for quick select and the two calendar months */
.calendar-content {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: flex-start;
    padding: 10px;
    gap: 15px;
    flex: 1 1 auto;
    overflow: auto;
    box-sizing: border-box;
}

/* Individual calendar month area */
.calendar-area {
    display: flex;
    flex-direction: column;
    flex-shrink: 0; /* Prevent shrinking if space is tight */
    width: 280px; /* Fixed width for each calendar month */
    box-sizing: border-box;
    /* Removed padding as it's now applied to .calendar-content */
}

.calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    margin-bottom: 0; /* Gap handles spacing */
    border-bottom: 1px solid var(--border-color);
    padding-bottom: 10px;
}

/* Adjust header specific for two calendars */
.left-calendar-area .calendar-header {
    justify-content: flex-start; /* Align prev button and month-year left */
}
.left-calendar-area .month-year {
    flex-grow: 1; /* Push month-year to center */
    text-align: center;
}

.right-calendar-area .calendar-header {
    justify-content: flex-end; /* Align next button and month-year right */
}
.right-calendar-area .month-year {
    flex-grow: 1; /* Push month-year to center */
    text-align: center;
}


.month-year {
    font-size: 1.2em;
    font-weight: 600;
    color: var(--primary-color);
    display: flex;
    align-items: center;
    gap: 3px;
}

.nav-button {
    background: none;
    border: none;
    font-size: 1.5em;
    color: var(--light-text-color);
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 5px;
    transition: background-color 0.2s ease, color 0.2s ease;
    outline: none;
}

.nav-button:hover {
    background-color: var(--primary-light);
    color: var(--primary-color);
}

/* Year navigation buttons styling */
.calendar-header .nav-button.year-nav {
    font-size: 0.9em;
    padding: 2px 6px;
    margin: 0 4px;
    background: none;
    border: none;
    color: #888;
    cursor: pointer;
    vertical-align: middle;
    transition: color 0.2s;
}

.calendar-header .nav-button.year-nav:hover {
    color: #222;
    background: #f0f0f0;
    border-radius: 3px;
}

.calendar-header .month-year {
    margin: 0 8px;
    font-weight: 500;
    font-size: 1em;
}

.calendar-weekdays {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    text-align: center;
    font-size: 0.9em;
    color: var(--light-text-color);
    margin-bottom: 0;
    font-weight: 500;
    margin-top: 10px; /* Space between header and weekdays */
}

.calendar-weekdays > div {
    padding: 8px 0;
}

.calendar-days-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 5px;
   
    margin-top: 5px; /* Space between weekdays and days */
}

.calendar-day {
    padding: 10px 5px;
    text-align: center;
    cursor: pointer;
    border-radius: 6px;
    transition: background-color 0.2s ease, color 0.2s ease;
    font-size: 1em;
    font-weight: 400;
    color: var(--text-color);
    position: relative;
    overflow: hidden;
}

.calendar-day.empty {
    visibility: hidden;
    pointer-events: none;
}

.calendar-day:not(.empty):hover {
    background-color: var(--primary-light);
    color: var(--primary-color);
}

/* Selected Date Styles */
.calendar-day.selected,
.calendar-day.in-range,
.calendar-day.range-start,
.calendar-day.range-end {
    z-index: 2;
    position: relative;
}

.calendar-day.selected {
    background-color: var(--primary-color) !important;
    color: var(--white) !important;
    font-weight: 600;
    box-shadow: none;
    border: none !important;
}

.calendar-day.in-range {
    background-color: var(--primary-light) !important;
    color: var(--primary-color) !important;
    border-radius: 0 !important;
    border: none !important;
}

.calendar-day.range-start,
.calendar-day.range-end {
    background-color: var(--primary-color) !important;
    color: var(--white) !important;
}

.calendar-day.range-start {
    border-top-left-radius: 6px !important;
    border-bottom-left-radius: 6px !important;
}

.calendar-day.range-end {
    border-top-right-radius: 6px !important;
    border-bottom-right-radius: 6px !important;
}

.calendar-day.range-start.range-end {
    border-radius: 6px !important;
}

/* Ensure selected/in-range override out-of-range */
.calendar-day.selected.out-of-range,
.calendar-day.in-range.out-of-range {
    background-color: rgb(227, 242, 253) !important;
    color: var(--primary-color) !important;
    pointer-events: auto;
    cursor: pointer;
    box-shadow: none;
    border: none !important;
}

.calendar-day.selected.out-of-range.range-start {
    background-color: rgb(25, 118, 210) !important;
    color: #fff !important;
}

.calendar-day.selected.out-of-range.range-end {
    background-color: rgb(25, 118, 210) !important;
    color: #fff !important;
}

/* Quick Select Section (on left) */
.quick-select-section {
    width: 120px;
    border-right: 1px solid var(--border-color);
    padding-right: 15px; /* Add padding to the right for separator */
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    gap: 8px;
    flex-shrink: 0; /* Prevent shrinking */
    height: 100%;
}

.quick-select-button {
    background-color: var(--background-color);
    color: var(--light-text-color);
    border: 1px solid var(--border-color);
    padding: 8px 12px;
    border-radius: 6px;
    cursor: pointer;
    font-size: 0.9em;
    transition: background-color 0.2s ease, color 0.2s ease, border-color 0.2s ease;
    outline: none;
    text-align: left;
    width: 100%;
    box-sizing: border-box;
}

.quick-select-button:hover {
    background-color: var(--primary-light);
    color: var(--primary-color);
    border-color: var(--primary-color);
}

.quick-select-button:active {
    background-color: var(--primary-color);
    color: var(--white);
    border-color: var(--primary-color);
}

.calendar-footer {
    display: flex;
    justify-content: flex-end;
    gap: 8px;
    padding: 8px 15px 12px 15px;
    border-top: 1px solid #eee;
    background: #fafafa;
    flex-shrink: 0; /* Prevent shrinking */
    box-sizing: border-box;
}

.apply-btn, .cancel-btn {
    padding: 8px 22px;
    font-size: 1em;
    border-radius: 6px;
    border: none;
    cursor: pointer;
    font-weight: 500;
    transition: background 0.2s, color 0.2s, box-shadow 0.2s;
    outline: none;
}

.apply-btn {
    background: var(--primary-color);
    color: var(--white);
    box-shadow: 0 2px 8px rgba(25,118,210,0.08); /* blue shadow */
}

.apply-btn:hover, .apply-btn:focus {
    background: #115293;
    color: #fff;
}

.cancel-btn {
    background: #f3f3f3;
    color: var(--light-text-color);
    border: 1px solid var(--border-color);
}

.cancel-btn:hover, .cancel-btn:focus {
    background: #e0e0e0;
    color: #222;
    border-color: #bbb;
}

/* Reset Button Styles */
.calendar-bottom-bar {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 18px;
    padding-top: 10px;
    border-top: 1px solid var(--border-color);
    background: transparent;
}

.reset-button {
    background: #ffe066;
    color: #222b4a;
    border: 2px solid #ffe066;
    border-radius: 6px;
    padding: 8px 18px 8px 12px;
    font-size: 1em;
    font-weight: normal;
    margin-left: 0;
    cursor: pointer;
    box-shadow: 0 2px 8px #ffe06655;
    transition: background 0.2s, box-shadow 0.2s, transform 0.1s, border-color 0.2s, color 0.2s;
    outline: none;
    display: flex;
    align-items: center;
    gap: 8px;
   
    width: 104px;
    min-height: 30px;
    box-sizing: border-box;
}

.reset-icon-bg {
    display: none;
}

.reset-button:hover, .reset-button:focus {
    background: #ffd600;
    border-color: #ffd600;
    color: #222b4a;
    box-shadow: 0 4px 16px #ffe06699;
    transform: translateY(-2px) scale(1.04);
}

.reset-button:hover .reset-icon-bg, .reset-button:focus .reset-icon-bg {
    background: #fffde7;
    box-shadow: 0 2px 8px #ffe06655;
    transform: scale(1.08) rotate(-10deg);
}

.calendar-day.out-of-range {
    color: #b0b0b0; /* A modern, soft grey */
    background: #fff !important; /* White background for unselectable days */
    cursor: not-allowed !important;
    pointer-events: none;
}